form{
    background: linear-gradient(180deg, #243b57, #142236);
    border: 2px solid #05D1FA;
    padding: 10px 20px;
    border-radius: 20px;
    filter: drop-shadow(5px 5px 25px rgba(0, 0, 0, 0.982));
}
.form-close-button
{
    color: white;
    font-size: 20px;
    filter: drop-shadow(0px 0px 5px #142236);
}
.form-close-button:hover{
    
    filter: drop-shadow(0px 0px 5px black);
    cursor: pointer;
}
.radios
{
    margin-top: 20px;
    font-size: 20px;
    filter: drop-shadow(0px 0px 5px black);
}

@media screen and (max-width:600px){
    .radios
    {
        font-size: 15px;
    }
}

@media screen and (max-width:500px){
    .radios
    {
        font-size: 10px;
    }
}
.closeBtn
{
    width: 20px;
    filter: drop-shadow(0px 0px 5px black);
}
.closeBtn:hover{
    width: 22px;
}